<template>
  <div id="top-header">
   <dv-decoration-3 style="width:30%;height:50px;" />
   <dv-decoration-3 style="width:30%;height:50px;" />

    <div class="center-title">JNRS柔性自动线</div>
    <span  style="position: fixed;right:10px;top:50px">{{currentTime}}</span>
  </div>
</template>

<script>
export default {
  name: 'TopHeader',
  data () {
    return {
      timer: '', // 定义一个定时器的变量
      currentTime: '' // 获取当前时间
    }
  },
  created () {
    // let _this = this // 声明一个变量指向Vue实例this，保证作用域一致
    this.timer = setInterval(() => {
      this.currentTime = this.$moment(new Date()).format('YYYY.MM.DD HH:mm:ss')
    }, 1000)
  },
  beforeDestroy () {
    if (this.timer) {
      clearInterval(this.timer)
    }
  }
}

</script>

<style lang="less">
#top-header {
  position: relative;
  width: 100%;
  height: 9%;
  display: flex;
  justify-content: space-between;
  flex-shrink: 0;

  .header-center-decoration {
    width: 40%;
    height: 60px;
    margin-top: 30px;
  }

  .header-left-decoration, .header-right-decoration {
    width: 25%;
    height: 60px;
  }

  .center-title {
    position: absolute;
    font-size: 30px;
    font-weight: bold;
    left: 50%;
    top: 15px;
    transform: translateX(-50%);
  }
}
</style>
