<template>
  <div id="middletop">
    <dv-border-box-10>
      <div class="rose-chart-title">
        <label style="text-align:center;width:100%">小时产量</label>
      </div>
      <div style="width: 100%;height: 180px;" id="main3"></div>
    </dv-border-box-10>
  </div>
  <!-- <dv-charts :option="option" /> -->
</template>

<script>
export default {
  name: "RoseChart",
  data() {
    return {};
  },
  methods: {
    echartsInit() {
      this.$axios({
        method: "post",
        url: "/api/services/app/Visual/YieldChart",
        //get传参
        // params: {
        //   ProgramFilter: "3333",
        // },
        //post传参
        data: {
          // StateFilter: -1,
          // TypeFilter: -1,
        },
      }).then((res) => {
        if (res.data.result == null) {
          this.$echarts.init(document.getElementById("main3"));
        } else {
          this.$echarts.init(document.getElementById("main3")).setOption({
            title: {
              text: "",
            },
            tooltip: {
              trigger: "axis",
            },

            grid: {
              left: "3%",
              right: "4%",
              bottom: "1%",
              top: "5%",
              containLabel: true,
            },

            xAxis: {
              type: "category",
              boundaryGap: false,
              data: res.data.result.resultHour.date,
              axisLabel: {
                interval: 0,
                // rotate:50,
                show: true,
                splitNumber: 15,
                textStyle: {
                  color: "rgba(255,255,255,.6)",
                  fontSize: "12",
                },
              },
            },
            yAxis: {
              type: "value",
              axisLabel: {
                interval: 0,
                // rotate:50,
                show: true,
                splitNumber: 15,
                textStyle: {
                  color: "rgba(255,255,255,.6)",
                  fontSize: "12",
                },
              },
            },
            series: [
              // {
              //     name: '',
              //     type: 'line',
              //     stack: '总量',
              //     color: '#EE843C',
              //     data: data[0].value
              // },
              {
                name: "",
                type: "line",
                stack: "总量",
                color: "#FFC30D",
                data: res.data.result.resultHour.qualifiedYield,
                itemStyle: {
                  normal: {
                    opacity: 1,
                    barBorderRadius: 5,
                    label: {
                      show: true, //开启显示
                      position: "top", //在上方显示
                      textStyle: {
                        //数值样式
                        color: "white",
                        fontSize: 10,
                      },
                    },
                  },
                },
              },
            ],
          });
        }
      });
    },
  },
  mounted() {
    this.echartsInit();
    setInterval(this.echartsInit, 10000);
  },
};
</script>

<style lang="less">
#middletop {
  width: 100%;
  height: 98%;
  background-color: rgba(6, 30, 93, 0.5);
  border-top: 2px solid rgba(1, 153, 209, 0.5);
  box-sizing: border-box;

  .rose-chart-title {
    height: 50px;
    font-weight: bold;
    text-indent: 20px;
    font-size: 20px;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }

  .dv-charts-container {
    height: calc(~"100% - 50px");
  }
  /* CSS Document */
}
</style>
