<template>
  <div id="middletop">
    <dv-border-box-10>
      <div class="rose-chart-title">
        <label style="text-align:center;width:100%">状态统计</label>
      </div>
      <dv-active-ring-chart :config="config" style="width:100%;height:70%" />
    </dv-border-box-10>
  </div>
  <!-- <dv-charts :option="option" /> -->
</template>

<script>
export default {
  name: "RoseChart",
  data() {
    return {
      config: {
      }
    };
  },
  methods: {
    chartinit() {
      this.$axios({
        method: "post",
        url: "/api/services/app/Visual/StateStatisticsChart",
        //get传参
        // params: {
        //   ProgramFilter: "3333",
        // },
        //post传参
        data: {
          //ProgramFilter: "3333",
        },
      }).then((res) => {
        var chartdata = [];
        res.data.result.forEach(element => {
          var listdata={
            name:element.name,
            value:Number(element.value) 
          }
          chartdata.push(listdata);
        });
        this.config = {
          radius: "80%",
          activeRadius: "90%",
          color: ["#00FF00", "#FF0000", "#FFFF00", "#0000FF", "#BEBEBE"],
          data: chartdata,
        };
      });
    },
  },
  mounted() {
    this.chartinit();
    setInterval(this.chartinit, 31000);
  },
};
</script>

<style lang="less">
#middletop {
  width: 100%;
  height: 98%;
  margin-bottom: 1%;
  margin-top: 1%;
  background-color: rgba(6, 30, 93, 0.5);
  border-top: 2px solid rgba(1, 153, 209, 0.5);
  box-sizing: border-box;

  .rose-chart-title {
    height: 50px;
    font-weight: bold;
    text-indent: 20px;
    font-size: 20px;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }

  .dv-charts-container {
    height: calc(~"100% - 50px");
  }
  /* CSS Document */
}
</style>
