<template>
  <div id="rose-chart">
    <dv-border-box-10>
      <div class="rose-chart-title">
        <label style="text-align:center;width:100%">班次产量</label>
      </div>
      <div class="center-data">
        <div style="width: 90%;height: 200px;" id="main2"></div>
      </div>
    </dv-border-box-10>
  </div>
  <!-- <dv-charts :option="option" /> -->
</template>

<script>
export default {
  name: "RoseChart",
  data() {
    return {};
  },
  methods: {
    echartsInit() {
      this.$axios({
        method: "post",
        url: "/api/services/app/Visual/YieldChart",
        //get传参
        // params: {
        //   ProgramFilter: "3333",
        // },
        //post传参
        data: {
          //  MinClockTimeFilter: -1,
          //  MaxClockTimeFilter: -1,
        },
      }).then((res) => {
        if (res.data.result == null) {
          this.$echarts.init(document.getElementById("main2"));
        } else {
          this.$echarts.init(document.getElementById("main2")).setOption({
            tooltip: {
              trigger: "axis",
              axisPointer: {
                type: "shadow",
              },
            },
            legend: {
              data: ["周产量"],

              icon: "circle", //  这个字段控制形状  类型包括 circle，rect ，roundRect，triangle，diamond，pin，arrow，none

              itemWidth: 10, // 设置宽度

              itemHeight: 10, // 设置高度

              itemGap: 40, // 设置间距
              textStyle: {
                color: "rgba(255,255,255,.6)",
                fontSize: "12",
              },
            },
            grid: {
              left: "0%",
              top: "35px",
              right: "0%",
              bottom: "4%",
              containLabel: true,
            },
            xAxis: [
              {
                type: "category",
                data: res.data.result.resultShift.date,
                axisLine: {
                  show: true,
                  lineStyle: {
                    color: "rgba(255,255,255,.1)",
                    width: 1,
                    type: "solid",
                  },
                },

                axisTick: {
                  show: false,
                },
                axisLabel: {
                  interval: 0,
                  // rotate:50,
                  show: true,
                  splitNumber: 15,
                  textStyle: {
                    color: "rgba(255,255,255,.6)",
                    fontSize: "12",
                  },
                },
              },
            ],
            yAxis: [
              {
                type: "value",
                axisLabel: {
                  //formatter: '{value} %'
                  show: true,
                  textStyle: {
                    color: "rgba(255,255,255,.6)",
                    fontSize: "12",
                  },
                },
                axisTick: {
                  show: false,
                },
                axisLine: {
                  show: true,
                  lineStyle: {
                    color: "rgba(255,255,255,.1	)",
                    width: 1,
                    type: "solid",
                  },
                },
                splitLine: {
                  lineStyle: {
                    color: "rgba(255,255,255,.1)",
                  },
                },
              },
            ],
            series: [
              {
                name: "产量",
                type: "bar",
                data: res.data.result.resultShift.qualifiedYield,
                barWidth: "35%", //柱子宽度
                // barGap: 1, //柱子之间间距
                itemStyle: {
                  normal: {
                    color: "#7FFF00",
                    opacity: 1,
                    barBorderRadius: 5,
                    label: {
                      show: true, //开启显示
                      position: "top", //在上方显示
                      textStyle: {
                        //数值样式
                        color: "white",
                        fontSize: 10,
                      },
                    },
                  },
                },
              },
            ],
          });
        }
      });
    },
  },
  mounted() {
    this.echartsInit();
    setInterval(this.echartsInit, 10000);
  },
};
</script>

<style lang="less">
#rose-chart {
  width: 100%;
  height: 100%;
  background-color: rgba(6, 30, 93, 0.5);
  border-top: 2px solid rgba(1, 153, 209, 0.5);
  box-sizing: border-box;

  .rose-chart-title {
    height: 50px;
    font-weight: bold;
    text-indent: 20px;
    font-size: 20px;
    display: flex;
    align-items: center;
  }
  .center-data {
    width: 100%;
    height: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .dv-charts-container {
    height: calc(~"100% - 50px");
  }
  /* CSS Document */
}
</style>
