<template>
  <div id="leftbottom">
    <dv-border-box-10>
      <div class="rose-chart-title">
        <label style="text-align:center;width:100%">报警履历</label>
      </div>
      <dv-scroll-board :config="config" style="width:100%;height:70%" />
    </dv-border-box-10>
  </div>
  <!-- <dv-charts :option="option" /> -->
</template>

<script>
export default {
  name: "RoseChart",
  data() {
    return {
      config: {},
    };
  },
  methods: {
    chartinit() {
      this.$axios({
        method: "post",
        url: "/api/services/app/Visual/AlarmGetAll",
        //get传参
        // params: {
        //   ProgramFilter: "3333",
        // },
        //post传参
        data: {
          StateFilter: -1,
          TypeFilter: -1,
        },
      }).then((res) => {
        var alarmdata = [];
        res.data.result.forEach((element) => {
          var arraydata = [];
          arraydata.push(element.alarm.startTime);
          arraydata.push(element.alarm.machineName);
          arraydata.push(element.alarm.code + ":" + element.alarm.message);
          alarmdata.push(arraydata);
        });
        this.config = {
          header: ["时间", "设备", "报警内容"],
          data: alarmdata,
          index: true,
          columnWidth: [50, 160, 100],
          align: ["center"],
          rowNum: 5,
          headerBGC: "#1981f6",
          headerHeight: 45,
          oddRowBGC: "rgba(0, 44, 81, 0.8)",
          evenRowBGC: "rgba(10, 29, 50, 0.8)",
        };
      });
    },
  },
  mounted() {
    this.chartinit();
    setInterval(this.chartinit, 30000);
  },
};
</script>

<style lang="less">
#leftbottom {
  width: 99%;
  height: 100%;
  background-color: rgba(6, 30, 93, 0.5);
  border-top: 2px solid rgba(1, 153, 209, 0.5);
  box-sizing: border-box;

  .rose-chart-title {
    height: 50px;
    font-weight: bold;
    text-indent: 20px;
    font-size: 20px;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }

  .dv-charts-container {
    height: calc(~"100% - 50px");
  }
  /* CSS Document */
}
</style>
