<template>
  <div id="leftbottom">
    <dv-border-box-10>
      <div class="rose-chart-title">
        <label style="text-align:center;width:100%">24小时状态</label>
      </div>
      <div style="width: 100%;height: 200px;" id="main"></div>
    </dv-border-box-10>
  </div>
  <!-- <dv-charts :option="option" /> -->
</template>

<script>
import echarts from "echarts";
export default {
  name: "RoseChart",
  data() {
    return {};
  },
  methods: {
    echartsinit() {
      var chart = document.getElementById("main");

      var myChart = this.$echarts.init(chart);
      this.$axios({
        method: "post",
        url: "/api/services/app/Visual/TFHourState",
        //get传参
        // params: {
        //   ProgramFilter: "3333",
        // },
        //post传参
        data: {
          //ProgramFilter: "3333",
        },
      }).then((res) => {
        // 这里使用了ES6的语法
        //console.log(res.data.result.chartData); // 请求成功返回的数据
        if (myChart == undefined) {
          myChart = echarts.init(chart);
        }
        var colors = ["#008000", "#FF0000", "#FFA500", "#0000FF", "#888888"];
        var state = ["运行", "报警", "暂停", "空闲", "关机"];
        var option = {
          animation: false,
          color: colors,
          tooltip: {
            textStyle: {
              color: "rgba(255,255,255,.6)",
              fontSize: "12",
            },
            formatter: function(params) {
              console.log(params);
              return (
                params.name +
                "：" +
                params.value[1] +
                "~" +
                params.value[2] +
                "<br/>" +
                "持续时间：" +
                (
                  (new Date(params.value[2]) - new Date(params.value[1])) /
                  (60 * 1000)
                ).toFixed(0) +
                "分钟"
              );
            }
          },
          legend: {
            data: state,
            top: "0px",
            selectedMode: false, // 图例设为不可点击
            textStyle: {
              color: "#fff",
            },
          },
          grid: {
            left: "3%",
            right: "3%",
            top: "10%",
            bottom: "10%",
            containLabel: true,
          },
          xAxis: {
            type: "time",
            interval: 3600 * 2 * 1000,
            axisLabel: {
              textStyle: {
                color: "rgba(255,255,255,.6)",
                fontSize: "12",
              },
              formatter: function(value) {
                var date = new Date(value);
                return getzf(date.getHours()) + ":" + getzf(date.getMinutes());
                function getzf(num) {
                  if (parseInt(num) < 10) {
                    num = "0" + num;
                  }
                  return num;
                }
              },
            },
          },
          //xAxis: {
          //    min: 0 // x轴零刻度对应的实际值
          //},
          yAxis: {
            data: res.data.result.machine,
            axisLabel: {
              textStyle: {
                color: "rgba(255,255,255,.6)",
                fontSize: "12",
              },
            },
          },
          series: [
            // 用空bar来显示四个图例
            { name: state[0], type: "bar", data: [] },
            { name: state[1], type: "bar", data: [] },
            { name: state[2], type: "bar", data: [] },
            { name: state[3], type: "bar", data: [] },
            { name: state[4], type: "bar", data: [] },
            {
              type: "custom",
              renderItem: function(params, api) {
                var categoryIndex = api.value(0);
                var start = api.coord([api.value(1), categoryIndex]);
                var end = api.coord([api.value(2), categoryIndex]);
                var height = 15;

                return {
                  type: "rect",
                  shape: echarts.graphic.clipRectByRect(
                    {
                      x: start[0],
                      y: start[1] - height / 2,
                      width: end[0] - start[0],
                      height: height,
                    },
                    {
                      x: params.coordSys.x,
                      y: params.coordSys.y,
                      width: params.coordSys.width,
                      height: params.coordSys.height,
                    }
                  ),
                  style: api.style(),
                };
              },
              encode: {
                x: [1, 2],
                y: 0,
              },
              data:
                //[{
                //    itemStyle: { normal: { color: colors[0] } },
                //    name: '正常',
                //    value: [0, '2019/6/1 2:00:11', '2019/6/1 24:00']
                //}]
                res.data.result.chartData,
            },
          ],
        };
        // 使用刚指定的配置项和数据显示图表。
        myChart.clear();
        myChart.setOption(option);
        window.addEventListener("resize", function() {
          myChart.resize();
        });
      });
    },
  },
  mounted() {
    this.echartsinit();
    //刷新数据
    setInterval(this.echartsinit, 30000);
  },
};
</script>

<style lang="less">
#leftbottom {
  width: 99%;
  height: 100%;
  background-color: rgba(6, 30, 93, 0.5);
  border-top: 2px solid rgba(1, 153, 209, 0.5);
  box-sizing: border-box;

  .rose-chart-title {
    height: 50px;
    font-weight: bold;
    text-indent: 20px;
    font-size: 20px;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }

  .dv-charts-container {
    height: calc(~"100% - 50px");
  }
  /* CSS Document */
}
</style>
